.footer-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
  padding: 20px 50px;
  background-color: #f1f1f185;


  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;

  @media screen and (max-width: 800px) {
    display: flex;
    justify-content: space-between;
  }

  @media screen and (max-width: 600px) {
    padding: 20px;
  }

  .footer-social-media {
    display: flex;
    justify-content: flex-start;

    .icon {
      height: 20px;
      width: 20px;
      margin-right: 15px;

      @media screen and (max-width: 600px) {
        margin-right: 10px;
      }

      &:hover {
        cursor: pointer;
        transform: translateY(2px);
        transition: all .2s;
      }
    }
  }

  .footer-middle-msg {
    text-align: center;

    @media screen and (max-width: 800px) {
      display: none;
    }
  }

  .footer-download-resume {
    display: flex;
    justify-content: flex-end;

    .download-button {
      padding: 10px 20px;
      font-size: 20px;
      font-weight: 600;
      background-image: linear-gradient(-45deg, #37c6ff 0%, #ff3083 50%, #7800a8 100%);
      background-size: 200% 200%;
      background-repeat: no-repeat;
      border: none;
      border-radius: 5px;
      color: #f1f1f1;

      &:hover {
        cursor: pointer;
        transform: translateY(2px);
        transition: all .2s;
      }
    }
  }
}